import { createContext, useState } from "react";

export const AppContext = createContext();

const AppProvider = ({ children }) => {
  const [activeNavBtn, setActiveNavBtn] = useState("Mięso");
  const [isEmpty, setIsEmpty] = useState(false);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [modalMode, setModalMode] = useState("");

  const switchModalMode = (mode) => setModalMode((prevVal) => (prevVal = mode));
  const switchIsEmpty = () => setIsEmpty((prevValue) => (prevValue = true));
  const toggleActiveNavBtn = (activeBtn) =>
    setActiveNavBtn((prevVal) => (prevVal = activeBtn));
  const toggleIsModalOpen = (bool) =>
    setIsModalOpen((prevValue) => (prevValue = bool));

  return (
    <AppContext.Provider
      value={{
        activeNavBtn,
        isEmpty,
        isModalOpen,
        modalMode,
        switchIsEmpty,
        switchModalMode,
        toggleActiveNavBtn,
        toggleIsModalOpen,
      }}
    >
      {children}
    </AppContext.Provider>
  );
};

export default AppProvider;
