import { useContext } from "react";

import { AppContext } from "../../store/store";

import { Wrapper, Content, CloseIcon, H1, Info } from "./Modal.css";

function Modal() {
  const { isModalOpen, modalMode, toggleIsModalOpen } = useContext(AppContext);

  return isModalOpen ? (
    <Wrapper>
      <Content mode={modalMode}>
        <CloseIcon mode={modalMode} onClick={() => toggleIsModalOpen(false)}>
          OK
        </CloseIcon>
        {modalMode === "succes" && (
          <>
            <H1 mode={modalMode}>Powodzenie !!!</H1>{" "}
            <Info mode={modalMode}>
              Lista została wysłana i zapisana w schowku
            </Info>
          </>
        )}
        {modalMode === "warning" && (
          <>
            <H1 mode={modalMode}>Uwaga !!!</H1>{" "}
            <Info mode={modalMode}>Nie podano żadnych wartości . . .</Info>
          </>
        )}
        {modalMode === "error" && (
          <>
            <H1 mode={modalMode}>Bład !!!</H1>{" "}
            <Info mode={modalMode}>
              Nie udało się wysłać. Brak internetu. Spróbuj ponownie !!!
            </Info>
          </>
        )}
      </Content>
    </Wrapper>
  ) : null;
}
export default Modal;
