import { useContext } from "react";

import AppProvider, { AppContext } from "./store/store";

import Nav from "./components/Navigation/Nav";
import ProductList from "./components/productsList/ProductList";

import "./App.css";

import Modal from "./components/modal/Modal";

function App() {
  const { activeNavBtn } = useContext(AppContext);

  return (
    <div className="App">
      <Nav />
      <header className="App-header">
        {activeNavBtn === "Mięso" && <ProductList title={activeNavBtn} />}
        {activeNavBtn === "Makro" && <ProductList title={activeNavBtn} />}
        {activeNavBtn === "Warzywa" && <ProductList title={activeNavBtn} />}
        {activeNavBtn === "Kobe" && <ProductList title={activeNavBtn} />}
        {activeNavBtn === "Steki" && <ProductList title={activeNavBtn} />}
      </header>
      <Modal></Modal>
    </div>
  );
}

function RootApp() {
  return (
    <AppProvider>
      <App />
    </AppProvider>
  );
}

export default RootApp;
