import styled from "styled-components";

export const Wrapper = styled.aside`
  background-color: rgba(0, 0, 0, 0.8);
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 100;
`;

export const Content = styled.div`
  background: ${(props) => {
    if (props.mode === "warning") {
      return "rgb(255, 218, 144)";
    } else if (props.mode === "succes") {
      return "rgb(174, 255, 174)";
    } else {
      return "pink";
    }
  }};
  border-radius: 10%;
  position: absolute;
  margin: auto;
  width: 300px;
  height: 300px;
  font-weight: 600;
  box-shadow: gray;
  padding: 20px;
  text-align: center;
  color: red;
`;

export const CloseIcon = styled.div`
  position: absolute;
  padding: 8px 25px;
  border: ${(props) => {
    if (props.mode === "warning") {
      return "rgb(255, 123, 0) 2px solid";
    } else if (props.mode === "succes") {
      return "rgb(0, 168, 28) 2px solid";
    } else {
      return "red 2px solid";
    }
  }};
  border-radius: 10px;
  background-color: transparent;
  left: 50%;
  transform: translateX(-50%);
  bottom: 10%;
  cursor: pointer;
  font-size: 25px;
  color: ${(props) => {
    if (props.mode === "warning") {
      return "rgb(255, 123, 0)";
    } else if (props.mode === "succes") {
      return "rgb(0, 168, 28)";
    } else {
      return "red";
    }
  }};
`;

export const H1 = styled.h1`
  color: ${(props) => {
    if (props.mode === "warning") {
      return "rgb(255, 123, 0)";
    } else if (props.mode === "succes") {
      return "rgb(0, 168, 28)";
    } else {
      return "red";
    }
  }};
  font-size: 30px;
`;
export const Info = styled.h1`
  color: ${(props) => {
    if (props.mode === "warning") {
      return "rgb(255, 123, 0)";
    } else if (props.mode === "succes") {
      return "rgb(0, 168, 28)";
    } else {
      return "red";
    }
  }};
  font-size: 25px;
`;
