import { useContext } from "react";

import { AppContext } from "../../store/store";
import { LIST_NAMES } from "../../data/productListsData";

import "./Nav.css";

function Nav() {
  const { activeNavBtn, toggleActiveNavBtn } = useContext(AppContext);
  const buttons = LIST_NAMES.map((name) => (
    <button
      key={name}
      style={activeNavBtn === name ? { backgroundColor: "green" } : {}}
      onClick={() => toggleActiveNavBtn(name)}
    >
      {name}
    </button>
  ));

  return <nav>{buttons}</nav>;
}

export default Nav;
