import { Field } from "formik";
// import { useState } from "react";

function Product({ productsList, toggleActive, toggleRowState }) {
  const list = productsList.map((product) => (
    <tr
      key={product.name}
      style={
        !product.isEmpty
          ? { backgroundColor: "green" }
          : product.isDisable
          ? { backgroundColor: "red" }
          : {}
      }
    >
      <td className="product-name">
        <label key={product.name} htmlFor={product.name}>
          {product.name}{" "}
        </label>
        <i className="fas fa-check" onClick={() => toggleRowState(product)}></i>
      </td>
      <td>
        <Field
          id={product.id}
          name={`${product.name}.amount`}
          type="number"
          onBlur={(e) => toggleActive(e, product.id)}
        />
      </td>
      <td>
        <Field as="select" name={`${product.name}.unit`}>
          <option value="kg">kg</option>
          <option value="sz">sz</option>
          <option value="l">l</option>
          <option value="karton">karton</option>
        </Field>
      </td>
    </tr>
  ));
  return list;
}

export default Product;
