import {
  meatList,
  makroList,
  vegetablesList,
  seafoodList,
  steaksList,
} from "../../data/productListsData";

import { Formik, Form, FieldArray } from "formik";
import { useState, useContext } from "react";

import { AppContext } from "../../store/store";
import Product from "./Product";
import { sendForm } from "../fetch";

import "./produktList.css";

function ProductList() {
  const { activeNavBtn, switchModalMode, toggleIsModalOpen } =
    useContext(AppContext);

  let list = [];
  switch (activeNavBtn) {
    case "Mięso":
      list = meatList;
      break;
    case "Makro":
      list = makroList;
      break;
    case "Warzywa":
      list = vegetablesList;
      break;
    case "Kobe":
      list = seafoodList;
      break;
    case "Steki":
      list = steaksList;
      break;
    default:
      throw new Error("Nieprawidłowa nazwa pozycji w nawigacji");
  }

  const [products, setProduct] = useState(list);
  const initialValues = {};

  for (const { name, unit, amount } of products) {
    initialValues[name] = { amount, unit };
  }

  const toggleActive = (e, id) => {
    e.target.value === ""
      ? setProduct(
          products.map((product) =>
            product.id === Number(id) ? { ...product, isEmpty: true } : product
          )
        )
      : setProduct(
          products.map((product) =>
            product.id === Number(id) ? { ...product, isEmpty: false } : product
          )
        );
  };

  const resetForm = () => {
    setProduct(
      products.map((product) => ({
        ...product,
        isEmpty: true,
        isDisable: false,
      }))
    );
  };

  const toggleRowState = (product) => {
    setProduct(
      products.map((productRow) =>
        productRow.id === product.id
          ? { ...productRow, isDisable: !productRow.isDisable }
          : productRow
      )
    );
  };
  return (
    <Formik
      initialValues={initialValues}
      onSubmit={(value, actions) => {
        sendForm(value, activeNavBtn, switchModalMode, toggleIsModalOpen);
        actions.resetForm();
        resetForm();
      }}
    >
      <Form>
        <FieldArray name="products">
          <table>
            <tbody>
              <Product
                productsList={products}
                toggleActive={toggleActive}
                toggleRowState={toggleRowState}
              />
            </tbody>
          </table>
        </FieldArray>
        <button className="submit" type="submit">
          Wyślij
        </button>
      </Form>
    </Formik>
  );
}

export default ProductList;
