import axios from "axios";

export const sendForm = (data, title, switchModalMode, toggleIsModalOpen) => {
  if (checkForm(data)) {
    toggleIsModalOpen(true);
    switchModalMode("warning");

    return;
  }

  axios
    .post("http://localhost:8080/", { data, title })
    .then(function (response) {
      navigator.clipboard.writeText(response.data);
      toggleIsModalOpen(true);
      switchModalMode("succes");
    })
    .catch(function (error) {
      toggleIsModalOpen(true);
      switchModalMode("error");
      console.log(error);
    });
};
// https://rockabilly-order-list.herokuapp.com/

const checkForm = (data) => {
  let counter = 0;
  const productsLength = Object.keys(data).length;
  const productsList = Object.entries(data);
  for (const [, { amount }] of productsList) {
    if (amount === "") {
      counter++;
    }
  }
  const isEmptyForm = counter === productsLength;
  return isEmptyForm;
};
